<template src="./MusicUploader.html"></template>
<style src="./MusicUploader.css"></style>
<script>
    import { parseBlob } from 'music-metadata';

    // const screenWidth = window.screen.width;
    // const screenHeight = window.screen.height;
    // console.log(`–†–∞–∑—Ä–µ—à–µ–Ω–∏–µ —ç–∫—Ä–∞–Ω–∞: ${screenWidth}x${screenHeight}`);
    
    export default {
        name: 'MusicUploader',
        data() {
            return {
                PlayList: false,
                isLoading: false,
                totalFiles: 0,
                processedCount: 0,
                MAX_FILE_SIZE: 100 * 1024 * 1024, // 100MB,
                MAX_PLAYLIST_SIZE: 1000 * 1024 * 1024, // 1000MB,
                curFilesSize: 0,
            }
        },
        methods: {
            async handleFileSelect(event) {
                const files = Array.from(event.target.files);
                const audioFiles = files.filter(file => this.isAudio(file));

                if (audioFiles.length === 0) {
                    this.showError("–ù–µ –±—ã–ª–æ –≤—ã–±—Ä–∞–Ω–æ –Ω–∏ –æ–¥–Ω–æ–≥–æ –∞—É–¥–∏–æ—Ñ–∞–π–ª–∞.");
                    this.resetInput(event.target);
                    return;
                }

                let cSize = 0;
                files.forEach(function(item) { cSize += item.size; })

                if (cSize > this.MAX_PLAYLIST_SIZE) {
                    this.showError("–ü—Ä–µ–≤—ã—à–µ–Ω –ª–∏–º–∏—Ç –¥–æ—Å—Ç—É–ø–Ω–æ–π –ø–∞–º—è—Ç–∏! –ú–∞–∫—Å–∏–º–∞–ª—å–Ω—ã–π —Ä–∞–∑–º–µ—Ä - " + (this.MAX_PLAYLIST_SIZE / 1024 / 1024) + "MB.");
                    this.resetInput(event.target);
                    return;
                }

                this.isLoading = true;
                this.totalFiles = audioFiles.length;
                this.processedCount = 0;

                let validFiles = [];

                for (let afile of audioFiles) {
                    try {
                        const blob = afile.slice(0, afile.size);
                        let parsedData = await parseBlob(blob);

                        afile.common = parsedData.common;
                        validFiles.push(afile); // –î–æ–±–∞–≤–ª—è–µ–º –ø–æ–¥—Ö–æ–¥—è—â–∏–π —Ñ–∞–π–ª –≤ –º–∞—Å—Å–∏–≤
                    } catch (error) {
                        console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ —Ä–∞–∑–±–æ—Ä–µ —Ñ–∞–π–ª–∞:', error);
                    }
                    this.processedCount++;
                }

                this.isLoading = false;

                if (validFiles.length > 0) {
                    this.$emit('files-selected', validFiles);
                    this.PlayList = true;
                } else {
                    alert("–ù–µ –±—ã–ª–æ –≤—ã–±—Ä–∞–Ω–æ –Ω–∏ –æ–¥–Ω–æ–≥–æ –∞—É–¥–∏–æ—Ñ–∞–π–ª–∞.");
                }
            },
            isAudio(file) {
                if (file.size > this.MAX_FILE_SIZE) {
                    console.warn(`–§–∞–π–ª ${file.name} —Å–ª–∏—à–∫–æ–º –±–æ–ª—å—à–æ–π: ${file.size} bytes`);
                    return false;
                }

                // –ë–æ–ª–µ–µ –Ω–∞–¥–µ–∂–Ω–∞—è –ø—Ä–æ–≤–µ—Ä–∫–∞ –∞—É–¥–∏–æ—Ñ–∞–π–ª–æ–≤
                const audioTypes = [ 'audio/mpeg', 'audio/wav', 'audio/flac', 'audio/ogg', 'audio/aac', 'audio/x-m4a' ];
                const extension = file.name.toLowerCase().split('.').pop();
                const audioExtensions = ['mp3', 'wav', 'flac', 'ogg', 'aac', 'm4a'];
            
                return audioTypes.includes(file.type) || audioExtensions.includes(extension);
            },
            showError(message) {
                alert(message);
                console.error(message);
            },
            // –°–±—Ä–æ—Å input (–æ—Ç–∫—Ä—ã–≤–∞–µ—Ç –≤–æ–∑–º–æ–∂–Ω–æ—Å—Ç—å –ø–æ–≤—Ç–æ—Ä–Ω–æ –∑–∞–≥—Ä—É–∂–∞—Ç—å —Ç–µ-–∂–µ —Ñ–∞–π–ª—ã)
            resetInput(inputElement) {
                inputElement.value = '';
            }
        }
    }
</script>